import ReactDOM from 'react-dom';
import React from 'react';
import * as Space from 'react-spaces'

const Demo = () => (
  <Space.ViewPort>
    <Space.LeftResizable size="25%" trackSize={true} style={grayStyle}>
      {Description("Left")}
    </Space.LeftResizable>
  </Space.ViewPort>
);

const grayStyle = { backgroundColor: "#eee" };
const blueStyle = { backgroundColor: "#e0eeee" };

const Description = desc => (
  <Space.Centered>
    <strong style={{ fontFamily: "Roboto", fontSize: 14 }}>{desc}</strong>
    <Space.Info>
      {info => (
        <div style={{ fontFamily: "Roboto", fontSize: 12 }}>
          {info.width} x {info.height}
        </div>
      )}
    </Space.Info>
  </Space.Centered>

);

ReactDOM.render(<Demo />, document.getElementById('root'));
